@import "../../style.scss";

.rightBar{
@include themify($theme){

    flex: 3;
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    overflow: scroll;
    background-color: themed("bgSoft");

    @include Mobile {
      display: none;
  }

    &::-webkit-scrollbar{
        display: none;
    }
    
    .container{
          
          padding: 20px;

        .item{

            -webkit-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);    
            -moz-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);       
             box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);             
             padding: 20px;
             margin-bottom: 20px;
             background-color: themed("bg");
 

            span{
                
                  color:gray;
                  
            }

            .user{
                 
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 15px 0px;
                
               .userInfo{
                    
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    position: relative;

                  img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    object-fit: cover;
                  }

                  p{
                    color: gray;   
                    width: 180px;

                  }
                  
                  
                  span{

                   font-weight: 500;
                  
                   color: themed("textColor");
                  }

                  .online{
                    width: 12px;
                    height: 12px;
                    background-color: limegreen;
                    border-radius: 50%;
                    position: absolute;
                    top:0;
                    left:30px; 
                  }

               }
               
               .buttons{
                  
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    button{
                        border: none;
                        padding: 5px;
                        color: white;
                        cursor: pointer;

                        &:first-child{
                            background-color: #5271ff;
                        }
                        &:last-child{
                            background-color:  #f0544f;
                        }
                    }
                }
            }

        }
    }
  }
}