@import "../../style.scss";

.leftBar{
@include themify($theme){
    
    flex: 2;
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    overflow: scroll;
    background-color: themed("bg");
    color: themed("textColor");

    @include Mobile {
        display: none;
    }


    &::-webkit-scrollbar{
        display: none;
    }

    .container{
           
        padding: 20px;

        hr{
            margin: 20px 0px;
            border: none;
            height: 0.5px;
            background-color: themed("border");
        }

        .menu{
  
               display: flex;
               flex-direction: column;
               gap: 20px;

               .user{
                 
                display: flex;
                align-items: center;
                gap: 10px;

                img{
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    object-fit: cover;
                }
                span{
                //    font-size: 15px;
                }

            }

            .item{
                  display: flex;
                  align-items: center;
                  gap: 10px;
                
                  img{
                    width: 25px;
                    height: 25px;
                }
                
                span{
                    font-size: 14px;
                }
            }
        }
    }
  }  
}