@import "../../style.scss";

.profile{
   @include themify($theme){
    
    background-color: themed("bgSoft");

    .images{
  
        width: 100%;
        height: 300px;
        position: relative;

        .cover{
        
             width: 100%;
             height: 100%;
             object-fit: cover;
        }

        .profilePic{
 
            width: 200px;
            height: 200px;
            border-radius: 50%;
            object-fit: cover;
            position: absolute;
            left: 0;
            right: 0;
            margin: auto;
            top: 200px;

        }
     }
      
     .profileContainer{
           padding: 20px 70px;

           @include Mobile {
      
            padding: 10px;
        }

        .uInfo{
             
            height: 180px;
            -webkit-box-shadow: 0px 0px 25px -10px rgba(0,0,0,0.38);    
            -moz-box-shadow: 0px 0px 25px -10px rgba(0,0,0,0.38);       
             box-shadow: 0px 0px 25px -10px rgba(0,0,0,0.38);  
             background-color: themed("bg");
             color: themed("textColor");
             padding: 50px;
             display: flex;
             align-items: center;
             justify-content: space-between;
             margin-bottom: 20px;

             @include Mobile {
      
             flex-direction: column;
             height: 30vh;
             padding: 20px;
             margin-top: 100px;
            }

             .left{
                flex: 1;
                display: flex;
                gap: 10px;
             
                a{
                    color: themed("textColorSoft");
                }
            }
             
             .center{
                flex: 1;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 10px;

                span{
                    font-size: 25px;
                    font-weight: 500;
                }
                .info{
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content:space-around;
                  
                    .item{
                        
                        display: flex;
                        align-items: center;
                        gap: 5px;
                        color: themed("textColorSoft");
                        
                        span{
                         
                            font-size: 13px;

                            }
                     }
                }

                button{
                    background-color: #5271ff;
                    border: none;
                    color: white;
                    padding: 10px 20px;
                    border-radius: 5px;
                    cursor: pointer;
                  
                }
             }
             
             .right{
                flex: 1;
                display: flex;
                align-items: center;
                justify-content: flex-end; 
                gap: 10px;
            }
        }
     }
   
   
    } 

}